body {
  margin: 0;
  text-align: center;
}
.search-card {
  width: 100%;
  height: 200px;
}
.image-animation-card {
  width: 100%;
  height: 300px;
  position: relative;
}
.searchInput {
  width: 90%;
  border: solid 1px rgb(0, 0, 0, 0.3);
  outline: none;
  padding: 6px 15px;
  font-size: 1.1rem;
}
.after-search {
  max-width: 1000px;

  display: inline-flex;
  min-height: 250px;
  padding: 10px;
  justify-content: space-between;
  justify-content: space-around;
  margin: 1px auto;
  flex-wrap: wrap;
}
.after-search2 {
  max-width: 1000px;
  display: inline-flex;
  height: 250px;
  padding: 10px;
  justify-content: space-between;
  justify-content: space-around;
  margin: 1px auto;
  flex-wrap: wrap;
}
.popular-card {
  margin: 2px 10px;
}
.follow-card {
  width: 100%;
  height: 190px;
  margin: 0;
}
.total-2 {
  background: whitesmoke;
  margin: 20px 0;
  width: 100%;
  max-height: 450px;

  margin-bottom: 0;
}
.gallery-pics {
  display: flex;
  flex-wrap: wrap;
  max-width: 56%;
  padding: 8px;
  margin: 0px auto;
}
.gallery-card {
  width: 170px;
  height: 100px;
  margin: 4px;
  transition: ease-in-out 0.5s all;
  transform: scale(1);
}
.gallery-card:hover {
  transition: ease-in-out 0.5s all;
  opacity: 0.8;
  transform: scale(1.08);
}
.contact-and-email {
  display: inline-flex;

  width: 80%;
  max-height: 800px;
  margin: 6px auto;
}
.contact-us {
  max-width: 500px;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  color: white;
  margin: 10px;
}
.home-image {
  object-fit: cover;
  background-size: cover;
  transition: all 7s ease;
  object-position: inherit;
  animation: fade-in 0.5s linear;
  animation-fill-mode: forwards;
  opacity: 0;
}
.hamburger {
  display: none;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.dialog-style {
  margin: 40px auto;
  position: relative;
  padding: 0;
}
.dialog-style img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  background: yellow;
}
@media only screen and (max-width: 500px) {
  .homepage-subtitle {
    display: none;
  }
}
@media (max-width: 690px) {
  .total-2 {
    min-height: 550px;
  }
  .popular-card {
    margin: 10px;
  }
  .image-animation-card {
    height: 200px;
  }
  .search-card {
    height: 170px;
  }
  .searchInput {
    font-size: 0.9rem;
    margin-top: 0;
  }
  .learn-button {
    padding: 0;
  }
  .follow-card {
    height: 140px;
    padding: 0;
    padding-bottom: 10px;
  }
}
@media (max-width: 444px) {
  .total-2 {
    min-height: 800px;
  }
}
@media (max-width: 942px) {
  .gallery-pics {
    max-width: 60%;
  }
}
@media (max-width: 894px) {
  .gallery-pics {
    max-width: 70%;
  }
}
@media (max-width: 752px) {
  .gallery-pics {
    max-width: 80%;
  }
}
@media (max-width: 660px) {
  .gallery-pics {
    max-width: 85%;
    padding: 0;
  }
}
@media (max-width: 424px) {
  .gallery-pics {
    max-width: 90%;
  }
  .gallery-card {
    width: 150px;
    height: 90px;
  }
  .follow-card h5 {
    font-size: 1.1rem;
  }
  .follow-card p {
    font-size: 0.8rem;
  }
  .contact-and-email h6 {
    font-size: 1.1rem;
  }
  .contact-and-email input {
    width: 300px;
    font-size: 0.9rem;
  }
}
@media (max-width: 354) {
  .gallery-pics {
    margin-top: 10px;
    width: 100%;
  }
}
@media (max-width: 738px) {
  .contact-and-email {
    flex-direction: column;
    margin: 10px auto;

    max-width: 400px;
    display: inline-flex;
  }
}
@media (max-width: 600px) {
  .links {
    color: white;
    width: 100%;
    text-align: right;
    position: absolute;
    top: 50px;
    left: 0;
    background: #4361ee;
    display: flex;
    flex-direction: column;
    clip-path: circle(30px at 90% -20%);
    transition: 0.6s all ease-in-out;
  }
  .open {
    clip-path: circle(1000px at 90% -10%);
  }
  .hamburger {
    display: block;
    min-width: 30px;
    max-height: 50px;
    cursor: pointer;
  }
  .line {
    width: 90%;
    height: 3px;
    background: white;
    margin: 4px 0px;
  }
}
@media only screen and (max-width: 351px) {
  .gallery-card {
    width: 45%;
    height: 90px;
  }
  .gallery-pics {
    width: 100%;
    padding: 0;
  }
  .contact-us h6 {
    font-size: 1.1rem;
  }
  .contact-us p {
    font-size: 0.8 rem;
  }
}
